@media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
   .container{
   	width:auto;
   }
}

@media (max-width:  $screen-sm-min) {
  body.modal-open {
    position: fixed;
    margin: 0;
    width: 100%;
    z-index: 10100;
  }
}

.modal-backdrop {
  position: fixed;
  z-index: 10200;
}

[data-dropdown-content] {
	display:none;
}

.open [data-dropdown-content] {
	display: block;
}

.modal-open .modal {
  z-index: 10300;
}
