.header-mini-cart {
    display: none;
    background: $sc-main-navigation-submenu-background-color;
    width: 320px;
    padding: $sc-padding-lv4;
    position: absolute;
    z-index: 1002;
    right: 0;
    box-shadow: 0 6px 12px transparentize($sc-neutral-shade-500, 1-0.175);
    border: 1px solid $sc-color-theme-border-light;

    &:before {
        position: absolute;
        top: -7px;
        right: 14px;
        display: inline-block;
        border-right: 7px solid transparent;
        border-bottom: 7px solid $sc-main-navigation-submenu-background-color;
        border-left: 7px solid transparent;
        border-bottom-color: transparentize($sc-neutral-shade-500, 1-0.2);
        content: '';
    }

    &:after {
        position: absolute;
        top: -6px;
        right: 15px;
        display: inline-block;
        border-right: 6px solid transparent;
        border-bottom: 6px solid $sc-color-theme-border-light;
        border-left: 6px solid transparent;
        content: '';
    }
}

.header-mini-cart {
    .items-views-selected-option-picker {
        display: none;
    }
    .items-views-selected-option-label,
    .items-views-selected-option-color p {
        margin-bottom: 0;
    }
}

.open .header-mini-cart {
    display: block;
    .header-mini-cart-empty a {
        color: $sc-header-cart-link-text-color;
    }
}

.header-mini-cart-menu-cart-legend {
    @extend .header-menu-cart-legend;
}

.header-mini-cart-container {
    max-height: 411px;
    overflow-y: auto;
    overflow-x: hidden;
    margin-bottom: $sc-margin-lv4;
}

.header-mini-cart-menu-cart-icon {
    @extend .header-menu-cart-icon;
}

.header-mini-cart-subtotal {
    margin-bottom: $sc-margin-lv4;
    overflow: hidden;
}

.header-mini-cart-subtotal-amount {
    @extend .col-xs-8;
    text-align: right;
    padding: 0px;
}

.header-mini-cart-subtotal-amount-text {
    text-transform: uppercase;
}

.header-mini-cart-subtotal-items {
    @extend .col-xs-4;
    padding: 0px;
    font-weight: $sc-font-weight-semibold;
    text-transform: uppercase;
}

.header-mini-cart a.header-mini-cart-button-view-cart {
    @extend .button-secondary;
    @extend .button-medium;
    @extend .button-generic;
    width: 100%;
    text-align: center;
}

a.header-mini-cart-buttons {
    display: block;
    clear: both;
}

.header-mini-cart .header-mini-cart-button-checkout {
    @extend .button-primary;
    @extend .button-medium;
    @extend .button-generic;
    width: 100%;
    text-align: center;
}

.header-mini-cart-buttons-left {
    @extend .col-xs-6;
    padding-left: 0px;
    padding-right: $sc-padding-lv1;
}

.header-mini-cart-buttons-right {
    @extend .col-xs-6;
    padding-right: 0px;
    padding-left: $sc-padding-lv1;
}

.header-mini-cart-menu-cart-link {
    @extend .header-menu-cart-link;
}

.header-mini-cart-menu-cart-link-enabled {
    @extend .header-menu-cart-link-enabled;
}

