footer {
    background: $sc-footer-background-color;
    border-top: 1px solid $sc-footer-border-color;
    color: $sc-footer-text-color;

    p {
        color: $sc-footer-text-color;
        margin-right: $sc-margin-lv2;
        margin-bottom: $sc-margin-lv2;
    }

    a {
        color: $sc-footer-text-color;
        margin-right: $sc-margin-lv2;

        &:hover {
            color: $sc-footer-link-color-hover;
        }

        &:active {
            color: $sc-footer-link-color-active;
        }
    }

    ul{
        @extend .col-md-8;
        @extend .col-xs-12;

        display:inline-block;
        list-style: none;
        margin: 0;
        margin-right: $sc-margin-lv2;
        font-weight: 300;
    }

    h1, h2, h3, h4, h5, h6 {
        color: $sc-footer-headings-color;
        margin-bottom: $sc-margin-lv4;
    }

    input {
        background: $sc-footer-fields-background-color;
        border: 1px solid $sc-footer-fields-border-color;
        color: $sc-footer-fields-text-color;
    }

    button, button.button-secondary, button.button-tertiary {
        @extend .button-medium;

        background: $sc-footer-buttons-background-color;
        color: $sc-footer-buttons-text-color;
        border: 1px solid $sc-footer-buttons-border-color;
    }
}

.footer-content{
    @extend .container;
    @extend .row;
    margin: auto;
    text-align: center;
    padding: $sc-padding-lv6 0 0;

    @media (min-width: $screen-sm-min) {
        padding-top: $sc-padding-lv8;
        padding-bottom: $sc-padding-lv8;
    }

    @media (min-width: $screen-md-min) {
        text-align: left;
    }
}

.footer-copyright-content {
    width: 100%;
    padding: $sc-padding-lv4 0;
    background: $sc-top-menu-background-color;
    .footer-copyright {
        @extend .container;
        color: $sc-footer-text-color;
        text-align: left;
    }
    * {
        padding-bottom: 0;
        margin-bottom: 0;
    }
}


.footer-content-col2 {
    @extend .col-md-2;
}

.footer-content-col3{
    @extend .col-md-3;
}

.footer-content-col4 {
    @extend .col-md-4;
}

.footer-content-col8{
    @extend .col-md-8;
}

.footer-content-col12{
    @extend .col-md-12;
}

