@import 'variables';

@font-face {
  font-family: 'FontAwesome Brands';
  font-style: normal;
  font-weight: 900;
  font-display: $fa-font-display;
  src: url('#{$fa-font-custom-path}/fa-brands-400.eot');
  src: url('#{$fa-font-path}/fa-brands-400.eot?#iefix') format('embedded-opentype'),
  url('#{$fa-font-custom-path}/fa-brands-400.woff2') format('woff2'),
  url('#{$fa-font-custom-path}/fa-brands-400.woff') format('woff'),
  url('#{$fa-font-custom-path}/fa-brands-400.ttf') format('truetype'),
  url('#{$fa-font-custom-path}/fa-brands-400.svg#fontawesome') format('svg');
}

@font-face {
  font-family: 'FontAwesome';
  font-style: normal;
  font-weight: 400;
  font-display: $fa-font-display;
  src: url('#{$fa-font-custom-path}/fa-regular-400.eot');
  src: url('#{$fa-font-path}/fa-regular-400.eot?#iefix') format('embedded-opentype'),
  url('#{$fa-font-custom-path}/fa-regular-400.woff2') format('woff2'),
  url('#{$fa-font-custom-path}/fa-regular-400.woff') format('woff'),
  url('#{$fa-font-custom-path}/fa-regular-400.ttf') format('truetype'),
  url('#{$fa-font-custom-path}/fa-regular-400.svg#fontawesome') format('svg');
}

@font-face {
  font-family: 'FontAwesome';
  font-style: normal;
  font-weight: 900;
  font-display: $fa-font-display;
  src: url('#{$fa-font-custom-path}/fa-solid-900.eot');
  src: url('#{$fa-font-path}/fa-solid-900.eot?#iefix') format('embedded-opentype'),
  url('#{$fa-font-custom-path}/fa-solid-900.woff2') format('woff2'),
  url('#{$fa-font-custom-path}/fa-solid-900.woff') format('woff'),
  url('#{$fa-font-custom-path}/fa-solid-900.ttf') format('truetype'),
  url('#{$fa-font-custom-path}/fa-solid-900.svg#fontawesome') format('svg');
}


.fa,
.fas {
  font-family: 'FontAwesome';
  font-weight: 900;
}

.far {
  font-family: 'FontAwesome';
  font-weight: 400;
}

.fab {
  font-family: 'FontAwesome Brands';
  font-weight: 400;
}

